:root {
    --main-color:#FFF; 
}

h1 {
    text-align: center;
    margin-bottom: 0.5em;
    font-size: var(--h1-size);
    /* margin-left: 1em; */
    padding: var(--h1-padding);
}

button {
    padding: 0.5em;
    width: 15%;
    min-width: 50px;
    text-align: center;
  }

button:active {
    transform: scale(1.1);
}

div#wtss-main {
    margin: 0 auto;

    padding: 1vw;

    

    background-color: #EEF0F2;

    /* border-radius: 10px; */

    flex: 2;
}

div#wtss-dialog-create-ticket {
    text-align: centre;
  }

div.wtss-ticket-detail-label {
    font-size: 1.5em;
    margin-top: 1em;
  }

  div#wtss-dialog-show-ticket div.wtss-buttons button {
    min-width: 110px;
  }

textarea {
    width: 100%;
    height: 5em;
  }

tr td:nth-child(4) {
    white-space: break-spaces;
  }

#wtss-main > * {
    text-align: center;
}

input.wtss-service-locator-search-field {
    width: 80%;
    height: var(--input-box-height);
    margin-bottom: 2em;
}

div.wtss-button input {
    padding:
  1em;
    margin:
  1em;
  }

  button.wtss-cancel-voucher {
    min-width: 110px;
    margin-bottom: 1em;
  }

  div.wtss-home-link {
    margin-top: 1em;
  }

/* Property list */

ul.wtss-fancy-selector {
    list-style: none;
    padding: 0;
    width: 70%;
    margin: auto;
}

li.wtss-site {
    list-style-type: none;
    border-radius: 10px;
    padding: 0.5em;
    margin: 0.2em;
    background-color: #EEF0F2;
    /* transition-duration: 0.3s; */
    text-align: center;
    display: flex;
}

li.wtss-site:hover {
    background-color: #c6c8ca;
    cursor: pointer;
    /* transform: scale(1.05); */
}

.wtss-site.wtss-selected {
    transform: scale(1.05);
    background-color: white;
    border: solid;
}

ul.wtss-fancy-selector li label {
    margin-left: auto;
    margin-right: auto;
}

/* make it so the cursor isnt pointing when the site is selected */
ul.wtss-select-site li.wtss-selected:hover {
    cursor: default;
}

li.wtss-site > span {
    justify-content: center;
    text-align: center;
    margin: auto 0 auto 0;
    margin-right: 0.3em;
}

li.wtss-site button.wtss-remove {
    height: var(--input-box-height);
    margin-left: auto;
    width: 10%;
    cursor: pointer;
}

/* service selector */

ul.wtss-fancy-selector li {
    margin: 0.5em;
    font-size: 1.1em;
    background:white;
    padding: 1em;
    border-radius: 10px;
    cursor: pointer;
    text-wrap: nowrap;
    transform: scale(0.9);
}

ul.wtss-fancy-selector li.wtss-selected {
    background: #009ADA;
    border-color: black;
    color: white;
    transform: scale(1.0);
    transition: 0.1s cubic-bezier(.73,-0.29,.49,1.57);
  }

  ul.wtss-fancy-selector li:hover {
    background:
  silver;
  }

  ul.wtss-fancy-selector li.wtss-selected:hover {
    background: #009ADA;
    border-color: black;
    color: white;
  }

  span.value {
    font-weight: bold;
  }


/* Site table */

table.wtss-service-locator-results {
    border-collapse: separate;
    border-spacing: 0 0.5em;

    width: 80%;

    margin: auto;

    text-align: left;

    
}

table.wtss-service-locator-results td {
    background-color: white;
    padding: 0.5em;
}

table.wtss-service-locator-results tbody tr:hover {
    transition: 0.3s;
    transition-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);

    transform: scale(1.1);
    
}

div:has(> table) {
    overflow-x: auto;
}

tr:hover {
    background:aliceblue;
}

.wtss-buttons > a{
    padding: 1em;
  }

div.wtss-buttons input.wtss-submit {
    margin-left: auto;
    cursor: pointer;
    
  }

  div.wtss-buttons input.wtss-submit.wtss-disabled {
    cursor: default;
  }

  li.wtss-site button.wtss-remove {
    display: none;
}

li.wtss-selected button.wtss-remove {
    display: inline-block;
}

/* Search again */

div.wtss-search-again {
    margin: 3vh;
    
}

div.wtss-search-again a {
    background-color: #BBB;
    color: black;

    padding: 1em;
    
    border-radius: 4px;

    text-decoration: none;
}

div.wtss-search-again a:hover {
    /* background-color: #1999D6; */
    border: solid;
    font-size: 1.1em;

    transition: 0.3s;
    transition-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);
} 

/* Phone entry */

input#wtss-phone {
    height: var(--input-box-height);
    width: 40%;
    min-width: 140px;
    margin: 1em;
}

.wtss-buttons {
    margin-top: 1em;
}

/* customer form */

.wtss-form-container {
    margin-top: 1.5em;
}

form#request-cust-details .wtss-field {
    margin: 0.5em;
}

input.wtss-submit, button.wtss-cancel {
    height: var(--input-box-height);    
    width: 15%;
    min-width: 70px;
    margin: 0.5em;                            
}

form#request-cust-details .wtss-buttons > * {
    margin: 0.5em;
    width: 20%;
}

div.wtss-button {
    margin: 1em;
}

div.wtss-form div.wtss-field {
    margin: 1em;
}

div.wtss-form div.wtss-field div.wtss-label {
    margin: 0.2em;
}

button.wtss-more {
    min-width: 140px;
    display: grid;
    margin:
  0 auto 0 auto;
  }

.wtss-problem {
    color: red;
    font-weight: bolder;
}

/* site view */

#wtss-dialog-site-main {
    text-align: left;
    display: flex;
    flex-wrap: wrap;
}

#wtss-dialog-site-main > :has(*) {
    width: 45%;
    background: white;
    border-radius: 10px;
    margin: 1em auto 0 auto;
    padding: 1em;
}

.wtss-section-title-bar {
    background-color: white;
    padding: 1em;
    border: solid;
    border-radius: 10px;
    text-align: left;
    width: 95% !important;
    max-width: 73vw;
    margin: 0 auto;
}

.wtss-component.wtss-services-menu {
    background: white;
    padding: 1em;
    margin-top: 1em;
    border-radius: 10px;
}

div.wtss-service-menu > * {
    display: flex;
    padding: 1em;
}

div.wtss-service-menu > * > * {
    flex: 1;
    margin: auto 0 auto 0;
}

div.wtss-service-menu > * > * > * {
    margin: auto 0 auto auto;
}

div.wtss-service-menu > * > * > button {
    /* width: 30%; */
}

.wtss-voucher-type:hover,
.wtss-ticket-type:hover 
{
    background: aliceblue;
}

div.wtss-voucher-number {
    font-size: large;
    font-weight: bold;
}

div.wtss-ticket-detail-data  textarea{
    width: calc(100% - 3em);
    margin: 1em;
}

/* mobile */

@media (max-width: 1200px) {

    #wtss-dialog-site-main {
        padding: 0.2em;
    }

    div#wtss-main {
        border-radius: 10px;
        width: 85%;
        padding: 1em;
        /* height: 77vh; */
    }

    div.wtss-service-menu > * {
        padding: 1em 1em 1em 0;
    }

    div.wtss-service-menu > * > * > button {
        width: 3.5em;
    }

    li.wtss-site button.wtss-remove {
        width: 20vw;
        position: absolute;
        top: 5px;
        right: 2px;
    }

    ul.wtss-fancy-selector li {
        text-wrap: nowrap;
        text-overflow: clip;
        overflow: clip;
        font-size: 1em;
    }

    input.wtss-submit {
        width: 20%;
    }

    div.wtss-entitlement {
        font-size: 0.7em;
    }
    
}

@media (max-width: 750px) {

    #wtss-dialog-site-main > :has(*) {
        width: 90%;
    }

    ul.wtss-fancy-selector {
        width: 90%;
    }

}
